.parent{
  border: 1px solid #aaa;
  padding: 20px;
  text-align: center;
  width: 794px;
  height: 1123px;
}

.inner{
   display:inline-block;
   border: 0px dashed #ccc;
   padding: 20px;
   text-align: center;
   font-family: "ＭＳ 明朝,平成明朝";
}

/* フォント */
.fontfamily{
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
img.n-font{
  height: 20pt;
  position: relative;
  top: 4px;
}
.font7{
  font-size: 7pt;
}
.font10{
  font-size: 10pt;
}
.font11{
  font-size: 11pt;
}
.font12{
  font-size: 12pt;
}
.font20{
  font-size: 20pt;
  padding: 15px 0px;     /* たぶん [上下]px [左右]px */
}

/* 各要素 */
.centerize{
  display: inline-block;
}
.subheader {
  text-align: left;
}
.titleheader {
  display: block;
}
.contentbody {
  display: table-cell;
}
.contentbottom {
  display: block;
}
.tablefooter {
  display: block;
}
.left {
  text-align: left;
}

/* メインの表 */
.fileTable {
  border-collapse: collapse;
  table-layout: fixed;
}
.fileTable th,
.fileTable td {
  border: 1px solid #000000;
  height: 15.2mm;
  text-align: center;
}
.fileTable td.name-phone {          /* 名前と携帯番号を入れるセル */
  line-height: 18px;
  vertical-align: bottom;
}
.fileTable td.left {                /* 左揃えのセル */
  text-align: left;
}
.fileTable td.right-borderless {    /* 右側の罫線をなくす */
  border-right: 0px;
}
.fileTable td.left-borderless {     /* 左側の罫線をなくす */
  border-left: 0px;
}
.fileTable td.side-borderless {     /* 両側の罫線をなくす */
  border-right: 0px;
  border-left: 0px;
}
.fileTable td.borderless {          /* 18列確保するダミーのセル */
  padding: 0px 0px;   /* たぶん [上下]px [左右]px */
  width: 35px;
  height: 0px;
  border: 0;
}

/* 職員記入欄の表 */
.authTable {
  border-collapse: collapse;
  table-layout: fixed;
  border: 1px solid #000000;
  text-align: center;
  margin-left: auto;
  margin-right: 0px;
}
.authTable th,
.authTable td.member {
  /* padding: 0px 10px; */
  width: 80px;
  border: 1px solid #000000;
  height: 8.2mm;
}
.authTable td.stamp {
  border: 1px solid #000000;
  height: 18.8mm;
}
